<template>
  <div class="container">
    <div class="row justify-content-center project-title">
      <div class="col-sm-12 col-md-8">
        <div class="row info-button noselect">
          <div class="col-12">
            <h6 class="m-0">{{ title }}</h6>
          </div>

          <div class="col-12">
            <p class="yeartext m-0">{{ year }}</p>
          </div>
          <div class="col-12" v-if="thereIsMedium">
            <p class="yeartext m-0">{{ medium }}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="row justify-content-center mb-5">
      <div class="col" v-if="thereIsVideo">
        <vue-plyr>
          <div data-plyr-provider="vimeo" :data-plyr-embed-id="videoId"></div>
        </vue-plyr>
      </div>

      <div v-if="thereAreImages">
        <div
          v-for="(image, index) in gallery"
          :key="index"
          class="row justify-content-center align-items-center mb-4 pb-5"
        
        >
          <div class="col-auto">
            <img
              data-expand="-20"
              :data-src="image.image1.url"
              alt=""
              class="lazyload img-fluid max-image-height"
            
            />
          </div>
        </div>
      </div>

      <div class="row justify-content-center project-title pt-3 p-0">
        <div class="col-sm-12 col-md-8">
          <div class="col">
            <prismic-rich-text :field="text" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectItem",

  data() {
    return {
      linkResolver: this.$prismic.linkResolver,
      post: [],
      title: [],
      text: [],
      year: [],
      field: [],
      gallery: [],
      image: [],
      medium: [],
      thereIsVideo: false,
      thereAreImages: false,
      thereIsMedium: false,
      videoId: {},
      toggleInfo: false,
    };
  },
  methods: {
    getContent(uid) {
      //Query to get post content
      this.$prismic.client.getByUID("post", uid).then((post) => {
        if (post) {
          this.displayImages();
          this.title = post.data.title[0].text;
          this.text = post.data.text;
          this.post.image = post.data.image.url;
          this.gallery = post.data.gallery;
          this.videoId = post.data.video.html;
          this.year = post.data.year[0].text;
          this.medium = post.data.medium[0].text;
          this.displayMedium();
          this.displayVideo();
          this.displayImages();
        } else {
          //returns error page
          this.$router.push({ name: "not-found" });
        }
      });
    },
    displayVideo() {
      if (this.videoId !== undefined) {
        this.thereIsVideo = true;
      } else this.thereIsVideo = false;
    },
    displayImages() {
      if (this.gallery !== undefined) {
        this.thereAreImages = true;
      } else this.thereAreImages = false;
    },
    displayMedium() {
      if (this.medium !== undefined) {
        this.thereIsMedium = true;
      } else this.thereIsMedium = false;
    },
  },
  created() {
    this.getContent(this.$route.params.uid);
  },
};
</script>

<style >
.yeartext {
  color: grey;
}

.project-wrapper {
  background-color: black;
}

.max-image-height {
  max-height: 80vh;
}
</style>

