import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import { createPrismic } from "@prismicio/vue";
import DrawnHeader from "@/components/ui/DrawnHeader.vue";
import TypeHeader from "@/components/ui/TypeHeader.vue";
import "lazysizes";
import RandomImage from "@/components/animation/RandomImage.vue";
import VuePlyr from 'vue-plyr'
import 'vue-plyr/dist/vue-plyr.css'
import DrawButton from "@/components/ui/DrawButton.vue";
import DrawButtonActive from "@/components/ui/DrawButtonActive.vue";


const prismic = createPrismic({
  endpoint: "https://roxanakenjeeva.cdn.prismic.io/api/v2",
});

const app = createApp(App);
app.use(router);
app.use(prismic);


app
  .use(VuePlyr, {
    plyr: {}
  })
app.component("DrawnHeader", DrawnHeader);
app.component("TypeHeader", TypeHeader);

app.component("RandomImage", RandomImage);
app.component("DrawButton", DrawButton);
app.component("DrawButtonActive", DrawButtonActive);

app.mount("#app");
