<script>


export default {

  data() {
    return {
      width: 0,
      scrollPosition: null,
      windowTop: 0,
      scrolled: "0",
      hovering: null
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    subIsActive(input) {
      const paths = Array.isArray(input) ? input : [input];
      return paths.some((path) => {
        return this.$route.path.indexOf(path) === 0;
      });
    },
    handleResize() {
      this.width = window.innerWidth;
      if (this.width < 576) {
        return true;
      } else {
        return false;
      }
    },
    handleScroll() {
      this.windowTop = window.top.scrollY;
      if (this.windowTop > 0) this.scrolled = "1";
      else if (this.windowTop < 1) this.scrolled = "0";
    },
    hoveringOver(el) {
      
      this.hovering = el;
    }
  },
};
</script>

