<template>
  <div class="container mb-5">
    <RandomImage />
    <div class="categories">
      <div class="d-flex justify-content-center">
        <div class="mx-3">
          <input type="radio" id="all" name="cat" @click="setFilter" checked />
          <label
            for="all"
            :class="[!filters.all ? 'category-inactive' : 'category-active']"
            @mouseenter="hoveringOver('all')"
            @mouseleave="hoveringOver(null)"
          >
            <div v-if="hovering === 'all'" class="draw draw-all">
              <DrawButton />
            </div>
            <div v-if="filters.all && hovering === null">
            <DrawButtonActive  class="draw draw-all" /></div>
            All</label
          >
        </div>
        <div class="mx-3">
          <input type="radio" id="animation" name="cat" @click="setFilter" />
          <label
            for="animation"
            :class="[
              !filters.animation ? 'category-inactive' : 'category-active',
            ]"
            @mouseenter="hoveringOver('animation')"
            @mouseleave="hoveringOver(null)"
          >
            <div v-if="hovering === 'animation'" class="draw">
              <DrawButton />
            </div>
            <div v-if="filters.animation && hovering === null">
            <DrawButtonActive key="DrawButtonActive" class="draw" /></div>
            Animation</label
          >
        </div>
        <div class="mx-3">
          <input type="radio" id="editorial" name="cat" @click="setFilter" />

          <label
            for="editorial"
            :class="[
              !filters.editorial ? 'category-inactive' : 'category-active',
            ]"
            @mouseenter="hoveringOver('editorial')"
            @mouseleave="hoveringOver(null)"
          >
            <div v-if="hovering === 'editorial'" class="draw">
              <DrawButton />
            </div>
            <div v-if="filters.editorial && hovering === null">
            <DrawButtonActive key="DrawButtonActive" class="draw" /></div>
            Editorial</label
          >
        </div>
        <div class="mx-3">
          <input type="radio" id="illustration" name="cat" @click="setFilter" />
          <label
            for="illustration"
            :class="[
              !filters.illustration ? 'category-inactive' : 'category-active',
            ]"
            @mouseenter="hoveringOver('illustration')"
            @mouseleave="hoveringOver(null)"
          >
            <div v-if="hovering === 'illustration'" class="draw">
              <DrawButton />
            </div>
            <div v-if="filters.illustration && hovering === null">
            <DrawButtonActive key="DrawButtonActive" class="draw" /></div>
            Illustration</label
          >
        </div>
      </div>
    </div>

    <div
      v-for="(post, index) in filteredProjects"
      :key="post.id"
      :id="post.id"
      ref="post"
      :post="index"
      class="row"
      :class="postClass(index) + ' ' + minusOffsetTop(index)"
    >
      <router-link
        :to="projects + post.uid"
        tag="div"
        :class="[
          post.data.image.url === undefined
            ? ''
            : landscapePortrait(
                post.data.image.dimensions.width,
                post.data.image.dimensions.height
              ),

          ,
        ]"
        class="col-xs-12"
      >
        <div class="row justify-content-center mx-1">
          <div
            class="
              d-flex
              justify-content-center
              align-items-center
              project-wrapper
              p-0
            "
          >
            <img
              class="img-fluid lazyload project-image"
              :data-src="post.data.image.url"
              data-expand="-20"
              alt=""
              @mouseenter="addClass"
              @mouseleave="removeClass"
            />
            <div class="text-wrapper">
              <div class="col-12">
                <h6 class="title-text m-0 mt-2">
                  {{ post.data.title[0].text }}
                </h6>
              </div>
              <div class="col m-0 p-0">
                <p class="title-year mt-2 m-0">
                  {{ post.data.year[0].text }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectList",

  data() {
    return {
      hovering: null,
      filters: {
        all: true,
        animation: false,
        editorial: false,
        illustration: false,
      },
      posts: [],
      linkResolver: this.$prismic.linkResolver,
      hover: -1,
      projects: "/projects/",
      isLoaded: false,
      currentHover: 100,
      centerClass: [0, 3, 6, 9, 12],
      leftClass: [1, 4, 7, 10, 13],
      rightClass: [2, 5, 8, 11, 14],
    };
  },
  methods: {
    addClass(e) {
      e.target.classList.add("project-image-quick-fade");
    },
    getPosts() {
      //Query to get blog posts
      this.$prismic.client
        .query(this.$prismic.Predicates.at("document.type", "post"))
        .then((response) => {
          this.posts = response.results;
        });
    },
    postClass(index) {
      if (this.centerClass.includes(index)) return "justify-content-sm-center";
      else if (this.leftClass.includes(index))
        return "justify-content-sm-start";
      else if (this.rightClass.includes(index)) return "justify-content-sm-end";
    },
    landscapePortrait(width, height) {
      if (width > height) return "col-sm-4";
      else return "col-sm-3";
    },
    minusOffsetTop(index) {
      if (index > 0) return "offset-top";
      else return "";
    },
    hoveringOver(el) {
      this.hovering = el;
    },
    setFilter(event) {
      const inputId = event.target.id;
      const isActive = event.target.checked;
      const updatedFilters = {
        ...this.filters,
        [inputId]: isActive,
      };

      // select only one
      let updatedFiltersArray = [];

      for (let item in updatedFilters) {
        updatedFiltersArray.push({
          item,
          value: updatedFilters[item],
        });
      }

      updatedFiltersArray.forEach((item) => {
        if (inputId === item.item) {
          item.value === !item.value;
        } else item.value = false;
    
      });

      let object = {};
      for (let i = 0; i < updatedFiltersArray.length; i++) {
        object[updatedFiltersArray[i].item] = updatedFiltersArray[i].value;
      }
      this.filters = object;
    },
  },
  computed: {
    filteredProjects() {
      const posts = this.posts;

      return posts.filter((post) => {
        if (
          this.filters.animation &&
          post.data.category.includes("animation")
        ) {
          return true;
        }
        if (
          this.filters.editorial &&
          post.data.category.includes("editorial")
        ) {
          return true;
        }
        if (
          this.filters.illustration &&
          post.data.category.includes("illustration")
        ) {
          return true;
        }
        if (this.filters.all && post.data.category.includes("a")) {
          return true;
        }
        return false;
      });
    },
  },

  created() {
    this.getPosts();
  },
};
</script>

